body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#root {
  display: grid;
  grid-template-columns: 1fr 4fr 1fr;
}

.examples {
  grid-column: 2;
}


.example-container {
  display: grid;
  width: 60%;
  min-width: 800px;
  margin-left: auto;
  margin-right: auto;
  grid-template-columns: 1fr;
  grid-gap: 1em;
  padding: 1em;
  background-color: #e2e2e2;
  border-radius: 0.5em;
}



.example-container > div {
  border: 1px solid #a1a1a1;
  border-radius: 0.5em;
  padding: 1em;
}

pre {
  background-color: #e2e2e2;
  border-radius: 0.5em;
  padding: 1em;
  font-size: 80%;
  font-weight: bold;
  font-family: monospace, sans-serif;
}

p {
  margin-left: 0.5em;
}
.date-picker {
  margin-top: 1.5em;
}

dl {
  margin-left: 1em;
}
dl > dt {
  font-weight: bold;
}
.yardtp-timepicker {
  display: grid;
  grid-template-columns: 1fr 4fr 1fr;
  margin-bottom: 0.5em; }
  .yardtp-timepicker label > button {
    font-weight: bolder;
    font-size: 110%;
    width: 100%;
    min-height: 2em; }
  .yardtp-timepicker div {
    text-align: center;
    font-weight: bold;
    font-size: 140%;
    color: #476155;
    padding-top: 0.15em;
    height: 2em; }

.yardtp-datepicker {
  min-width: 280px; }
  .yardtp-datepicker .calendar-days {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-gap: 0.2em;
    grid-auto-rows: minmax(1em, auto);
    margin-bottom: 0.5em; }
    .yardtp-datepicker .calendar-days .day-1 {
      grid-column: 1; }
    .yardtp-datepicker .calendar-days .day-2 {
      grid-column: 2; }
    .yardtp-datepicker .calendar-days .day-3 {
      grid-column: 3; }
    .yardtp-datepicker .calendar-days .day-4 {
      grid-column: 4; }
    .yardtp-datepicker .calendar-days .day-5 {
      grid-column: 5; }
    .yardtp-datepicker .calendar-days .day-6 {
      grid-column: 6; }
    .yardtp-datepicker .calendar-days .week-1 {
      grid-row: 1; }
    .yardtp-datepicker .calendar-days .week-2 {
      grid-row: 2; }
    .yardtp-datepicker .calendar-days .week-3 {
      grid-row: 3; }
    .yardtp-datepicker .calendar-days .week-4 {
      grid-row: 4; }
    .yardtp-datepicker .calendar-days .week-5 {
      grid-row: 5; }
    .yardtp-datepicker .calendar-days .day {
      cursor: pointer;
      border-radius: 0.5em;
      text-align: center;
      background-color: #a1baae;
      color: #387541;
      min-height: 3em;
      margin: 0; }
      .yardtp-datepicker .calendar-days .day span {
        display: inline-block;
        font-weight: normal;
        padding: 25%; }
    .yardtp-datepicker .calendar-days .day.out-of-range {
      cursor: default;
      color: #818181;
      background-color: #b4b4b4; }
    .yardtp-datepicker .calendar-days .day.previous-month {
      color: #818181;
      background-color: #cecece; }
    .yardtp-datepicker .calendar-days .day.next-month {
      color: #818181;
      background-color: #cecece; }
    .yardtp-datepicker .calendar-days .day.present {
      background-color: #749a88;
      color: black; }
    .yardtp-datepicker .calendar-days .day.selected {
      background-color: #72a179;
      color: #476155; }
  .yardtp-datepicker .day-name-headings {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-gap: 0.2em;
    grid-auto-rows: minmax(1em, auto); }
    .yardtp-datepicker .day-name-headings h4 {
      text-align: center;
      font-weight: bold;
      color: #31443b; }
  .yardtp-datepicker .month-select {
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    margin-bottom: 0.5em; }
    .yardtp-datepicker .month-select label > button {
      font-weight: bolder;
      font-size: 110%;
      width: 100%;
      min-height: 2em; }
    .yardtp-datepicker .month-select h3 {
      text-align: center;
      font-size: 125%;
      color: #476155; }

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

