body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#root {
  display: grid;
  grid-template-columns: 1fr 4fr 1fr;
}

.examples {
  grid-column: 2;
}


.example-container {
  display: grid;
  width: 60%;
  min-width: 800px;
  margin-left: auto;
  margin-right: auto;
  grid-template-columns: 1fr;
  grid-gap: 1em;
  padding: 1em;
  background-color: #e2e2e2;
  border-radius: 0.5em;
}



.example-container > div {
  border: 1px solid #a1a1a1;
  border-radius: 0.5em;
  padding: 1em;
}

pre {
  background-color: #e2e2e2;
  border-radius: 0.5em;
  padding: 1em;
  font-size: 80%;
  font-weight: bold;
  font-family: monospace, sans-serif;
}

p {
  margin-left: 0.5em;
}
.date-picker {
  margin-top: 1.5em;
}

dl {
  margin-left: 1em;
}
dl > dt {
  font-weight: bold;
}